import { useState, useRef } from 'react';
import { useHistory, Link, Redirect } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';

import PasswordInput from '../../components/PasswordInput';

import './Auth.css';

import logo from '../../logo.svg';

const Auth = () => {
    // states
    const [error, setError] = useState(null);
    const [loading, setLoading] = useState(false);

    // hooks
    const { Login, setCurrentUser, currentUser} = useAuth();
    const history = useHistory();

    // refs
    const emailRef = useRef();
    const passRef = useRef();
    

    // event handlers
    const handleChange = e => {
        if (!error) return;
        setError('');
    }

    // event handlers
    const handleSubmit = async e => {
        e.preventDefault();
        
        setLoading(true);

        // get form data
        const email = emailRef.current.value;
        const pass = passRef.current.value;

        try{
            const res = await Login(email, pass);
            

            // if successful login set the local storage and state
            localStorage.setItem('user', JSON.stringify('{"id":"604b84a03523674a98225fe8","username":"test","friends":{"accepted":[],"pending":[],"blocked":[]},"settings":{"darkMode":true,"activeStatus":true,"notifications":true},"avatar":"test-1615562045917.png"}'));
            setCurrentUser('{"id":"604b84a03523674a98225fe8","username":"test","friends":{"accepted":[],"pending":[],"blocked":[]},"settings":{"darkMode":true,"activeStatus":true,"notifications":true},"avatar":"test-1615562045917.png"}');

            // redirect on successfull login
            history.push('/friends');
        }
        catch(err){
            // get error object
            const errors = err.response.data.errors;
            console.log(errors);

            // set error state
            setError(errors);
        }
        finally{
            // stop the loading state
            setLoading(false);
        }
    }

    // if user is logged in redirect to main app
    if(currentUser) return <Redirect to="/friends" />

    return (
       <div className="form-container">
            <img className="auth-logo" src={logo} alt="Connect logo" />
            <h1>Let's sign you in.</h1>
            <form onSubmit={handleSubmit} onChange={handleChange} autoComplete="off">
                    <span className="error-message">{error?.login}</span>
                    <input type="text" ref={emailRef} placeholder="Email" />
                    <PasswordInput passRef={passRef} text="Password"/>
            <div className="inverted__link">
                <span>
                    Don't have an account? <Link to="/register">Register!</Link>
                </span>
                <button type="submit" disabled={loading}>Sign In</button>
            </div>
        </form>
       </div>
    );
}

export default Auth;